import opencvjs from '@/assets/opencv/opencvImport.js';

export type BarcodeImgStream = ImageData;

export type BarcodeResult = {
  corners: Float32Array;
  result?: string | null;
  output: Uint8ClampedArray
}

// cv 
let _cv: any = null;

function getCv() {
  if (_cv) {
    return Promise.resolve(_cv);
  } else {
    return opencvjs().then((res) => {
      console.log(res);
      _cv = res;
      return _cv;
    });
  }
}

self.onmessage = (event: MessageEvent<BarcodeImgStream>) => {
  const imgData = event.data;
  getCv().then((cv) => {
    if (cv) {
      // console.log(cv.imread);
      const src = cv.matFromImageData(imgData);

      // 
      let gray = new cv.Mat();
      cv.cvtColor(src, gray, cv.COLOR_RGBA2GRAY);

      // 
      // let denoised = new cv.Mat();
      // cv.medianBlur(gray, denoised, 1);
      let denoised  = new cv.Mat();
      cv.GaussianBlur(gray, denoised, new cv.Size(3, 3), 0, 0, cv.BORDER_DEFAULT);

      // 
      let sharpened = new cv.Mat();
      cv.addWeighted(gray, 1.5, denoised, -0.5, 0, sharpened);

      // 
      // let hist = new cv.Mat();
      // cv.equalizeHist(denoised, hist);

      // 
      // let threshold = new cv.Mat();
      // cv.adaptiveThreshold(sharpened, threshold, 255, cv.ADAPTIVE_THRESH_GAUSSIAN_C, cv.THRESH_BINARY, 3, 2);

      //  3x3 1
      // let kernel = cv.Mat.ones(1, 1, cv.CV_8U);

      // 
      // let erosion = new cv.Mat();
      // cv.erode(threshold, erosion , kernel, new cv.Point(-1, -1), 10, cv.BORDER_CONSTANT, cv.Scalar.all(0) );

      // let dilation = new cv.Mat();
      // cv.dilate(threshold, dilation , kernel, new cv.Point(-1, -1), 1, cv.BORDER_CONSTANT, cv.Scalar.all(255));

      // 
      // let binary = new cv.Mat();
      // cv.threshold(threshold, binary, 0, 255, cv.THRESH_BINARY + cv.THRESH_OTSU);

      // 
      // // 
      // let mask = new cv.Mat();
      // cv.threshold(gray, mask, 220, 255, cv.THRESH_BINARY);
      // //  inpainting 
      // let antiReflect = new cv.Mat();
      // cv.inpaint(denoised, mask, antiReflect, 21, cv.INPAINT_TELEA);

      let barcodeDetector = new cv.barcode_BarcodeDetector();
      let points = new cv.Mat();
      let result = barcodeDetector.detectAndDecode(sharpened, points);

      // 
      let output = new cv.Mat();
      cv.cvtColor(sharpened, output, cv.COLOR_RGBA2RGBA);
      const img = new Uint8ClampedArray(output.data);

      let corners = new Float32Array();

      if (result.length > 0) {
        console.log('barcode is: ', result);
        corners = new Float32Array(points.data32F);
      }

      self.postMessage({
        result,
        corners,
        output: img,
      }, [
        img.buffer,
        corners.buffer
      ]);

      // 
      src.delete();
      gray.delete();
      // mask.delete();
      // antiReflect.delete();
      // binary.delete();
      denoised.delete();
      sharpened.delete();
      barcodeDetector.delete();
      points.delete();
      output.delete();
    }
    else {
      self.postMessage({
        result: null,
        corners: null,
        output: null,
      });
    }
  });
};
